.slick-dots,
.slick-next,
.slick-prev {
	display: block;
	padding: 0
}

/* .slick-dots li button:before,
.slick-next:before,
.slick-prev:before {
	nothing here yet
} */

.slick-loading .slick-list {
	background: red
}

.slick-initialized {
	display: flex;
	width: 100%;
	position: relative;
}

