	src: url(fonts/slick.eot);
	src: url(fonts/slick.eot?#iefix) format('embedded-opentype'), url(fonts/slick.woff) format('woff'), url(fonts/slick.ttf) format('truetype'), url(fonts/slick.svg#slick) format('svg')
} */

.slick-next,
.slick-prev {
	position: absolute;
	top: 0;
	bottom: 0;
	z-index: 2;
	font-size: 0;
	line-height: 0;
	background-color: transparent;
	width: 40px;
	cursor: pointer;
	border: none;
	outline: 0;
}

.slick-track {
	display: flex;
}

.slick-list {
	overflow: hidden;
	position: relative;
	width: 100%;
	flex-shrink: 0;
	width: 100%;
}

.slick-next:focus,
.slick-next:hover,
.slick-prev:focus,
.slick-prev:hover {
	color: transparent;
	outline: 0;
}

.slick-next:focus:before,
.slick-next:hover:before,
.slick-prev:focus:before,
.slick-prev:hover:before {
	opacity: 1
}

.slick-next.slick-disabled:before,
.slick-prev.slick-disabled:before {
	opacity: .25
}

.slick-next:before,
.slick-prev:before {
	font-size: 20px;
	line-height: 1;
	opacity: .75;
	color: #fff;
	text-shadow: 1px 1px 2px rgb(51 51 51 / 0.4), 1px -1px 2px rgb(51 51 51 / 0.4), -1px -1px 2px rgb(51 51 51 / 0.4), -1px 2px 2px rgb(51 51 51 / 0.4);
}

.slick-prev {
	left: 0;
}

[dir=rtl] .slick-prev {
	right: 0;
	left: auto
}

.slick-prev:before {
	content: '<'
}

.slick-next:before,
[dir=rtl] .slick-prev:before {
	content: '>'
}

.slick-next {
	right: 0;
}

.slick-slide {
	display: flex;
	justify-content: center;
}

[dir=rtl] .slick-next {
	right: auto;
	left: 0
}

[dir=rtl] .slick-next:before {
	content: '←'
}

.slick-dotted.slick-slider {
	margin-bottom: 30px
}

.slick-dots {
	bottom: -25px;
	width: 100%;
	margin: 0;
	list-style: none;
	text-align: center
}

.slick-dots li {
	position: relative;
	display: inline-block;
	width: 20px;
	height: 20px;
	margin: 0 5px;
	padding: 0;
	cursor: pointer
}

.slick-dots li button {
	font-size: 0;
	line-height: 0;
	display: block;
	width: 20px;
	height: 20px;
	padding: 5px;
	cursor: pointer;
	color: transparent;
	border: 0;
	outline: 0;
	background: 0 0
}

.slick-dots li button:focus,
.slick-dots li button:hover {
	outline: 0
}

.slick-dots li button:focus:before,
.slick-dots li button:hover:before {
	opacity: 1
}

.slick-dots li button:before {
	font-size: 6px;
	line-height: 20px;
	position: absolute;
	top: 0;
	left: 0;
	width: 20px;
	height: 20px;
	content: '•';
	text-align: center;
	opacity: .25;
	color: #000
}

.slick-dots li.slick-active button:before {
	opacity: .75;
	color: #000
}


